import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { okaidia } from 'react-syntax-highlighter/dist/esm/styles/prism';

const CodeBlock = ({ code }) => {
    const copyToClipboard = () => {
        navigator.clipboard.writeText(code);
        alert('');
    };

    return (
        <div style={{ position: 'relative', marginBottom: '20px' }}>
            <SyntaxHighlighter language="javascript" style={okaidia}>
                {code}
            </SyntaxHighlighter>
            <button
                onClick={copyToClipboard}
                style={{ position: 'absolute', top: '10px', right: '10px' }}
            >
                
            </button>
        </div>
    );
};

const AirCursorDocumentation = () => (
    <div style={{ padding: '20px', color: '#000000' }}>
        <h2></h2>
        <p>AirCursor</p>
        <CodeBlock code={`npm install air-cursor`} language="bash" />
        <p>
            <code>package.json</code>
            <code>air-cursor</code>
        </p>

        <h2></h2>
        <p>AirCursor</p>
        <CodeBlock code={`npm update air-cursor`} language="bash" />
        <p>
        
        </p>
        <CodeBlock code={`npm install air-cursor@latest`} language="bash" />
        <p><code>`@latest`</code></p>
        <p>package.json</p>

        <h2></h2>
        <ol>
            <li>
                <h3>AirCursor</h3>
                <p>ReactAirCursor:</p>
                <CodeBlock code={`import AirCursor from 'air-cursor';`} />
            </li>
            <li>
                <h3>AirCursor</h3>
                <p>
                    JSX
                    <code>{`<AirCursor />`}</code>
                    AirCursor:
                </p>
                <CodeBlock code={`<AirCursor />`} language="jsx" />
            </li>
            <li>
                <h3>buttonText</h3>
                <p>
                    <code>{`<AirCursor />`}</code>
                    
                    <code>buttonText</code>
                    
                    "":
                </p>
                <CodeBlock code={`<AirCursor buttonText="" />`} language="jsx" />
            </li>
            <li>
                <h3></h3>
                <p>
                    
                    <code>{`<AirCursor />`}</code>
                    
                    
                </p>
            </li>
            <li>
                <h3></h3>
                <p>
                    
                </p>
            </li>
            <li>
                <h3></h3>
                <p>
                    
                    AirCursor
                </p>
            </li>
        </ol>
    </div>
);

export default AirCursorDocumentation;
